legend{
  width: auto;
  margin-bottom: 10px;
  border-bottom: none;
}
.has-prompt {
  position: relative;

  // Ensure icons don't overlap text
  .form-control {
    padding-left: (@input-height-base * 1.25);
  }
}

.form-control-prompt {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2; // Ensure icon is above input groups
  display: block;
  width: @input-height-base;
  height: @input-height-base;
  line-height: @input-height-base;
  text-align: center;
  pointer-events: none;
}
.input-lg + .form-control-prompt,
.input-group-lg + .form-control-prompt {
  width: @input-height-large;
  height: @input-height-large;
  line-height: @input-height-large;
}
.input-sm + .form-control-prompt,
.input-group-sm + .form-control-prompt {
  width: @input-height-small;
  height: @input-height-small;
  line-height: @input-height-small;
}

.form-clear-input{
  font-size: (@font-size-base * 1.5);
  font-weight: @close-font-weight;
  color: @close-color;
  .opacity(@opacity-base);
}
.form-control-feedback {
  z-index: 4;
}
.form-control-feedback.case-sensitive {
    font-weight: bold;
    text-decoration:underline
}
.case-toggle-btn + .form-control-feedback,.case-toggle-btn {
    width: 26px;
}
.case-toggle-btn {
    font-size: 12px;
}
.has-error {
  .input-group-btn .btn-default {
    color: @state-danger-text;
    border-color: @state-danger-text;
    background-color: @state-danger-bg;
  }
}